import { format } from 'date-fns'
import blogArticles from '~/components/blogArticles.vue'

export default {
  components: { blogArticles },
  filters: {
    formatDate () {
      return format(new Date(), 'dd-MM-yyyy')
    }
  },
  async asyncData ({ $content, params }) {
    const blogs = await $content('blog', params.slug).fetch()

    return {
      blogs
    }
  },
  data () {
    return {
      query: '',
      result: [],
      nosearch: true
    }
  },
  head () {
    const title = 'NuxtLover | Blog'
    const description = 'Awesome blog build by NuxtLover for Nuxters'
    return {
      title,
      meta: [
        {
          hid: 'description',
          name: 'description',
          content: description
        },
        // Open Graph
        {
          hid: 'og:title',
          property: 'og:title',
          content: title
        },
        {
          hid: 'og:description',
          property: 'og:description',
          content: description
        },
        // Twitter Card
        {
          hid: 'twitter:title',
          name: 'twitter:title',
          content: title
        },
        {
          hid: 'twitter:description',
          name: 'twitter:description',
          content: description
        }
      ]
    }
  },
  watch: {
    async query (query) {
      if (!query) {
        this.result = []
        return
      }

      this.result = await this.$content('blog')
        .only(['title', 'slug', 'description'])
        .sortBy('createdAt', 'asc')
        .limit(12)
        .search(query)
        .fetch()
    }
  },
  methods: {
    reset () {
      this.query = ''
    }
  }
}
